@import'https://use.fontawesome.com/releases/v5.13.0/css/all.css';.gallery{background-color:var(--color-black)}.gallery .gallery_text h2{color:var(--color-golden);font-size:40px;font-weight:700;margin-bottom:20px}.gallery .gallery_images{display:flex;flex-wrap:nowrap;overflow-x:scroll;gap:25px;padding:0 15px}.gallery .gallery_images::-webkit-scrollbar{display:none}.gallery .gallery_images .gallery_image{display:flex;align-items:center;position:relative;cursor:pointer;transition:var(--main-transition)}.gallery .gallery_images .gallery_image::before{content:"";position:absolute;width:100%;height:100%;top:100%;left:0;background-color:rgba(12,12,12,.5);transition:var(--main-transition)}.gallery .gallery_images .gallery_image:hover::before{top:0}.gallery .gallery_images .gallery_image:after{font-family:"Font Awesome 5 Brands";content:"ï…­";font-weight:400;font-size:30px;position:absolute;top:120%;left:50%;transform:translate(-50%, -50%);transition:var(--main-transition);color:var(--color-white)}.gallery .gallery_images .gallery_image:hover::after{top:50%}.gallery .gallery_images .gallery_image img{width:200px;height:285px;object-fit:cover}.gallery .scroll_button_right,.gallery .scroll_button_left{position:absolute;width:30px;height:30px;text-align:center;line-height:30px;background-color:rgba(12,12,12,.8);border-radius:50%;top:50%;transform:translateY(-50%);cursor:pointer;transition:var(--main-transition)}.gallery .scroll_button_right:active,.gallery .scroll_button_right:hover,.gallery .scroll_button_left:active,.gallery .scroll_button_left:hover{background-color:var(--color-black)}.gallery .scroll_button_right svg,.gallery .scroll_button_left svg{color:var(--color-golden)}.gallery .scroll_button_right{right:18px}.gallery .left{left:5px}